.header {
  display: flex;
  justify-content: space-between;
  position: fixed;
  align-items: center;
  padding: 1rem;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--primary-color);
  z-index: 10;
  margin-top: 0;
}

.nav-menu {
  display: flex;
}

.nav-menu li {
  padding: 0 1rem;
}
.nav-menu li a {
  font-size: 1.2rem;
  font-weight: 500;
}

.hamburger {
  display: none;
}

@media screen and (max-width: 1040px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: var(--primary-color);
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.3s;
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-menu li {
    padding: 1rem 0;
  }

  .nav-menu li a {
    font-size: 2rem;
  }

  .hamburger {
    display: initial;
  }
}
